@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.App {
    text-align: center;
}

.App-logo {
    height: 35vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 4s linear;
                animation: App-logo-spin infinite 4s linear;
    }
}

.App-header {
    background: radial-gradient( circle, #2A0944, #3B185F, #A12568, #3B185F);
    -webkit-filter: blur();
            filter: blur();
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    background-size: 300vw 300vw;
    -webkit-animation: colorir 3s ease infinite;
            animation: colorir 3s ease infinite;
    overflow: hidden;
    background-position: 50% 50%;
}

@-webkit-keyframes colorir {
    33.33% {
        background-position: 100% 50%;
    }
    66.66% {
        background-position: 200% 50%;
    }
    100% {
        background-position: 350% 50%;
    }
}

@keyframes colorir {
    33.33% {
        background-position: 100% 50%;
    }
    66.66% {
        background-position: 200% 50%;
    }
    100% {
        background-position: 350% 50%;
    }
}

@-webkit-keyframes run {
    0% {}
}

@keyframes run {
    0% {}
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: translateX(-300vmin);
                transform: translateX(-300vmin);
    }
    to {
        -webkit-transform: translateX(300vmin);
                transform: translateX(300vmin);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: translateX(-300vmin);
                transform: translateX(-300vmin);
    }
    to {
        -webkit-transform: translateX(300vmin);
                transform: translateX(300vmin);
    }
}

.titulo {
    margin-top: 15vmin;
}

.linha {
    margin-top: -5vmin;
}

.go {
    margin-top: 2vmin;
    height: 7vmin;
    width: 7vmin;
    min-width: 30px;
    min-height: 30px;
    font-size: 3vmin;
    background-color: transparent;
    border-radius: 180vmin;
    border-color: #FEC260;
}

button:hover {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
    border-color: transparent;
}

button:focus {
    -webkit-animation: aCima 2s ease;
            animation: aCima 2s ease;
}

@-webkit-keyframes aCima {
    33.33% {
        -webkit-transform: translateY(0vmin);
                transform: translateY(0vmin);
        border-color: transparent;
        opacity: 1;
    }
    80% {
        -webkit-transform: translateX(40vmin) translateY(-40vmin);
                transform: translateX(40vmin) translateY(-40vmin);
        border-color: transparent;
        opacity: 1;
    }
    100% {
        -webkit-transform: translateX(80vmin) translateY(-80vmin);
                transform: translateX(80vmin) translateY(-80vmin);
        opacity: 0;
        border-color: transparent;
        opacity: 1px;
    }
}

@keyframes aCima {
    33.33% {
        -webkit-transform: translateY(0vmin);
                transform: translateY(0vmin);
        border-color: transparent;
        opacity: 1;
    }
    80% {
        -webkit-transform: translateX(40vmin) translateY(-40vmin);
                transform: translateX(40vmin) translateY(-40vmin);
        border-color: transparent;
        opacity: 1;
    }
    100% {
        -webkit-transform: translateX(80vmin) translateY(-80vmin);
                transform: translateX(80vmin) translateY(-80vmin);
        opacity: 0;
        border-color: transparent;
        opacity: 1px;
    }
}

.tagNow {
    height: 4.2vmin;
    width: 7vmin;
    border-radius: 90vmin 0vmin 0vmin 90vmin;
    margin-left: auto;
    margin-top: 3vmin;
    font-size: 2vmin;
    -webkit-filter: blur();
            filter: blur();
}

.tagNowText {
    margin-top: -0.9vmin;
    margin-left: -3vmin;
}

.obs {
    margin-top: auto;
    font-size: 2.5vmin;
}
.navBar {
    position: relative;
    width: 100%;
    height: 47px;
    left: 0px;
    top: 0px;
    background: #9600BB;
    border: 1px solid rgba(255, 255, 255, 0.13);
    box-sizing: border-box;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    overflow-X: hidden;
    overflow: hidden;
}

.link {
    font-family: 'Lexend Deca', sans-serif;
    font-style: normal;
    font-size: 14px;
    text-align: right;
    color: #FFFFFF !important;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
}

